<template lang="html">
    <div class="search">
        <div class="search-box-wrapper">
            <search-box ref="searchBox" @query="onQueryChange"></search-box>
        </div>
        <div class="shortcut-wrapper" v-show="!query">
            <div class="shortcut">
                <div class="hot-key">
                    <h1 class="title">热门搜索</h1>
                    <ul>
                        <li v-for="(item,index) in hotkey" class="item" @click="addQuery(item.k)">
                            <span>{{item.k}}</span>
                        </li>
                    </ul>
                </div>
            </div>
        </div>
        <div class="search-result" v-show="query">
            <suggest :query="query" @listScroll="blurInput" @select="saveSearch"></suggest>
        </div>
        <router-view></router-view>
    </div>
</template>

<script>
import SearchBox from 'base/search-box/search-box'
import {getHotKey} from 'api/search'
import {ERR_OK} from 'api/config'
import Suggest from 'components/suggest/suggest'
import {mapActions} from 'vuex'
export default {
    created(){
        this._getHotKey()
    },
    components:{
        SearchBox,
        Suggest
    },
    data(){
        return {
            placeholder:'',
            hotkey:[],
            query:''
        }
    },
    methods:{
        async _getHotKey(){
            const result = await getHotKey()
            if(result.code === ERR_OK){
                this.hotkey = result.data.hotkey.slice(0,10)
            }
        },
        addQuery(query){
            this.$refs.searchBox.setQuery(query)//父级组件调用子组件的方法
        },
        onQueryChange(query){
            this.query = query
        },
        blurInput(){
            this.$refs.searchBox.blur()
        },
        saveSearch(){//触发保存结果
            this.saveSearchHistory(this.query)
        },
        ...mapActions([
            'saveSearchHistory'
        ])
    }
}
</script>

<style lang="stylus">
@import "../../common/stylus/variable"
@import "../../common/stylus/mixin"

.search
    .search-box-wrapper
        margin: 20px
    .shortcut-wrapper
        position: fixed
        top: 178px
        bottom: 0
        width: 100%
        .shortcut
            height: 100%
            overflow: hidden
            .hot-key
                margin: 0 20px 20px 20px
                .title
                    margin-bottom: 20px
                    font-size: $font-size-medium
                    color: $color-text-l
                .item
                    display: inline-block
                    padding: 5px 10px
                    margin: 0 20px 10px 0
                    border-radius: 6px
                    background: $color-highlight-background
                    font-size: $font-size-medium
                    color: $color-text-d
            .search-history
                position: relative
                margin: 0 20px
                .title
                    display: flex
                    align-items: center
                    height: 40px
                    font-size: $font-size-medium
                    color: $color-text-l
                    .text
                        flex: 1
                    .clear
                        extend-click()
                        .icon-clear
                            font-size: $font-size-medium
                            color: $color-text-d
    .search-result
        position: fixed
        width: 100%
        top: 178px
        bottom: 0
</style>



